body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  height: 90vh;
  background-color:#c0c6c9;
}

.title{
  font-size: 2.5rem;
  margin: 0.5rem 0 0 0;
  font-size: bold;
  color:#c0c6c9;
  text-shadow:2px 2px 4px #ffffff;
}

img{
  margin: 0rem;
}

ol, ul {
  padding-left: 0px;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
 
}
li{
  display: flex;
  gap: 0.5rem;
  
  
}
.options{
  display: flex;
  flex-direction: row-reverse;
  justify-content: start;
  position: relative;
}
.reverse{
  color: rgb(1, 151, 119);
  border-radius: 0.2rem;
  width: fit-content;
  height:max-content;
  font-size: 1rem;
  padding: 0.2rem 0.5rem;
  border: none;
  box-shadow: 1px 1px 5px #037562,
  inset -1px -1px 1px #037562;
  background: #fff;
  margin: 1rem 0 0 0;
  cursor: pointer;
  position: absolute;
  top: -1rem;
  right: 0rem;
}

.history{
  color: rgb(1, 151, 119);
  border-radius: 0.2rem;
  width: fit-content;
  height:max-content;
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
  border: none;
  box-shadow: 1px 1px 5px #037562,
  inset -1px -1px 1px #037562;
  background: #fff;
  margin: 1rem 0 0 0;
  cursor: pointer;
  
}

.boardRow:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin: 0px 0;
  color: #037562;
  /* text-shadow: -2px -2px 2px #ffffff; */
  border-radius: 0.5rem;
  font-weight: 500;
}
 


.mybutton{
  border: none;
  box-shadow: 2px 2px 5px #037562,
  inset 3px 3px 8px #ffffff;
  border-radius: 0.2rem;
  color:#037562;
  font-size: 0.9rem;
}

.gold{
  background: radial-gradient(ellipse farthest-corner at  bottom right, #ac71293f 0%, #F9E77D 50%, #D0A835 75%, #AC7029 100%), 
  radial-gradient(ellipse farthest-corner at top, #ac7129 0%, #F9E77D 50%, #D0A835 75%, #ac71298f 100%)!important;
  color:#ffffff!important;
  text-shadow: 1px 1px 5px rgba(155, 113, 22, 0.651);
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  color: #00a790;
  font-size: 3rem;
  font-weight: bold;
  line-height: 34px;
  
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 33%;
  height: 105px;
}

.active{
  background-color: #00a790;
  color: white;
}
.bold{
  font-weight: 900;
}

.square:focus {
  outline: none;
}

.kbdNavigation .square:focus {
  background: #d5d5d5;
}

.game {
  display: flex;
  flex-direction: column;
  gap:1rem;
  align-items: center;
}

.gameInfo {
  margin-left: 0px;
  display: flex;
  gap: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.gameBoard {
  width: 320px;
  height: 320px;
}
.status{
  font-size: 2rem;
}